.story-card-group {
  text-align: center;
}

.story-card-group__wrapper {
  @include reset-list;
  margin-left: auto;
  margin-right: auto;
  max-width: 68rem;

  @include respond-to('medium-large') {
    display: flex;
    margin-left: unit(-1);
    max-width: none;
    width: calc(100% + unit(1));
  }

  .story-card {
    @include respond-to('medium-large') {
      padding-left: unit(1);
      width: 25%;

      &.-big {
        width: 50%;
      }
    }

    + .story-card {
      margin-top: unit(1);

      @include respond-to('medium-large') {
        margin-top: 0;
      }
    }
  }
}
